.custom-swiper-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.custom-swiper-container .custom-navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  gap: 0.9375rem;
  margin-top: 3.75rem;
  margin-left: 1.875rem;
}
.custom-swiper-container .custom-navigation button {
  width: 10.625rem;
  height: 4.5625rem;
  background: none;
  border: 0.0625rem solid #848386;
  border-radius: 62.4375rem;
  cursor: pointer;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 120%;
  color: #1F1F1F;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: relative;
}
.custom-swiper-container .custom-navigation button:not(.swiper-button-disabled):hover {
  background-color: #b6b6b6;
}
.custom-swiper-container .custom-navigation button.swiper-button-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  color: #999;
}
.custom-swiper-container .custom-navigation .page-counter {
  font-size: 1.875rem;
  color: #0A0A0A;
  width: 0.75rem;
  text-align: center;
}

.custom-next {
  margin-left: 2.8rem;
}